<template>
  <v-container fluid>
    <!--<transaction-dialog :show="showDialog" @closed-dialog="showDialog = arguments[0]"></transaction-dialog>-->

    <v-dialog v-if="selectedTransaction" v-model="showDialog" fullscreen hide-overlay transition="dialog-bottom-transition">
      <v-toolbar dark>
        <v-btn icon dark @click="showDialog = false">
          <v-icon>fas fa-times</v-icon>
        </v-btn>
        <v-toolbar-title>Transacción #</v-toolbar-title>
        <v-spacer></v-spacer>
      </v-toolbar>

      <v-layout>
        <v-flex fill-height class="white black--text text-xs-center">
          <h1>Beneficiario:</h1>
          <v-chip class="large" outline selected>{{selectedTransaction.full_name}}</v-chip>
          <small>{{selectedTransaction.email}}</small>
          <h2>Identificación</h2>
          <span class="headline"><strong>{{selectedTransaction.identification.type}}</strong>-{{selectedTransaction.identification.number}}</span>
          <h1>Datos Bancarios</h1>
          <h3>Nº Cuenta</h3>
          <span>
            {{selectedTransaction.bank.code}}{{selectedTransaction.account_number}}
          </span>
          <h3>Banco</h3>
          <span>
            <v-icon color="black">{{selectedTransaction.bank.icon}}</v-icon>
            {{selectedTransaction.bank.name}} ● <strong>{{selectedTransaction.type == 'a' ? 'Ahorro' : 'Corriente'}}</strong>
          </span>
          <h1>Fondos</h1>
          <v-chip>
            <v-avatar class="teal">Bs.S</v-avatar>
            {{selectedTransaction.amount2}}
          </v-chip>
          <v-chip>
            <v-avatar class="teal">$</v-avatar>
            {{selectedTransaction.amount1}}
          </v-chip>
          <h2>Tasa</h2>
          <span>{{selectedTransaction.rate.value}} ● <small>{{selectedTransaction.rate.updated_at}}</small></span>
          <h1>Estado</h1>
          <strong>
            <span v-if="selectedTransaction.status" class="green--text darken-3">Pagado</span>
            <span v-else class="red--text darken-3">Pendiente</span>
          </strong>

        </v-flex>
      </v-layout>
    </v-dialog>

    <v-layout>
      <v-flex v-if="user.transactions.length < 1" class="black--text text-xs-center">
        <i class="black--text fas fa-frown fa-10x"></i>
        <h3>¡No hay transacciones pendientes!</h3>
      </v-flex>
      <v-flex v-else fill-height xs12 class="black--text text-xs-center">
        <v-list light three-line>
          <v-subheader>click en la transacción para mas detalles</v-subheader>
          <!--<v-divider></v-divider>-->
          <template v-for="(transaction, index) in user.transactions">
            <v-list-tile
              :key="transaction.account_number"
              @click="selectTransaction(transaction)"
            >
              <v-list-tile-avatar>
                <v-icon>{{transaction.bank.icon}}</v-icon>
              </v-list-tile-avatar>
              <v-list-tile-content>
                <v-list-tile-title>
                  {{transaction.bank.name}} ● {{transaction.type == 'a' ? 'Ahorro' : 'Corriente'}}
                </v-list-tile-title>
                <v-list-tile-title>
                  <strong>Beneficiario: </strong>{{ transaction.full_name }}
                </v-list-tile-title>
                <v-list-tile-sub-title>
                  <strong>Monto: </strong><span>{{transaction.amount2}} Bs.S</span>
                </v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-divider v-if="index + 1 < user.transactions.length"></v-divider>
          </template>
          <v-list-tile :ripple="false"></v-list-tile>
        </v-list>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
// import TransactionDialog from '../views/TransactionDialog'

export default {
  data () {
    return {
      showDialog: false,
      selectedTransaction: null
    }
  },
  computed: {
    user () {
      return this.$store.state.user
    }
  },
  methods: {
    selectTransaction (transaction) {
      this.selectedTransaction = transaction
      this.showDialog = true
    }
  }
}
</script>
