<template>
  <section class="home-page">
    <v-toolbar tabs>
        <v-toolbar-title>
          <img src="~@/assets/logo-min.png" alt="logo" height="22px">
          <span>ColSys</span>
        </v-toolbar-title>

        <v-spacer></v-spacer>

        <v-menu offset-y>
          <v-btn
            icon
            slot="activator"
          >
            <v-icon>fas fa-ellipsis-v</v-icon>
          </v-btn>
          <v-list>
            <v-list-tile
              :to="{name: 'about'}"
            >
              <v-list-tile-title>Acerca de</v-list-tile-title>
            </v-list-tile>
            <v-list-tile
              :to="{name: 'login'}"
            >
              <v-list-tile-title>Salir</v-list-tile-title>
            </v-list-tile>
          </v-list>
        </v-menu>

        <v-tabs
          slot="extension"
          v-model="tab"
          centered
          icons-and-text
          color="transparent"
          slider-color="white"
        >
          <v-tab
            v-for="(tab, index) in tabs"
            :key="'tab-' + index"
          >
            {{tab.text}}
            <v-icon>{{tab.icon}}</v-icon>
          </v-tab>
        </v-tabs>
      </v-toolbar>

      <v-tabs-items v-model="tab">
        <v-tab-item
          v-for="(tab, index) in tabs"
          :key="'tab-' + index"
        >
          <component :is="tab.component"></component>
        </v-tab-item>
      </v-tabs-items>

      <v-btn
            fixed
            dark
            fab
            bottom
            right
            :to="{name: 'add'}"
          >
        <v-icon>fas fa-plus</v-icon>
      </v-btn>

  </section>
</template>

<script>
  import InformationTab from '../components/InformationTab'
  import TransactionsTab from '../components/TransactionsTab'

export default {
  data () {
    return {
      tab: null,
      tabs: [
        {text: 'Información', icon: 'fas fa-info', component: InformationTab},
        {text: 'Transacciones', icon: 'fas fa-chart-line', component: TransactionsTab}
      ]
    }
  }
}
</script>
