<template>
  <v-container fluid fill-height>
    <v-layout colum>
      <v-flex xs12 class="black--text text-xs-center">
        <h1>Bienvenido:</h1>
        <v-chip class="large" outline selected>{{user.full_name}}</v-chip>
        <div v-if="!loading">
          <h1 class="primary--text">Tasa de cambio actual:</h1>
          <h3 class="display-3 font-weight-bold">{{rate.value}} Bs.S</h3>
          <h5 class="font-weight-light">
            ultima actualización: {{rate.updated_at}}
            <v-btn fab class="v-btn--extra-small" @click="update"><v-icon>fas fa-sync</v-icon></v-btn>
          </h5>
        </div>
        <div v-else class="mt-4">
          <i class="black--text fas fa-spinner fa-spin fa-10x"></i>
          <h3 class="font-weight-light">actualizando información...</h3>
        </div>
        <div>
          <h3 class="text-xs-center">Tus Estadísticas</h3>
          <h5 class="font-weight-light">Transacciones de hoy:
            <v-chip small color="yellow" class="black--text font-weight-bold">{{$store.getters.transactionsToday.length}}</v-chip>
          </h5>
          <h5 class="font-weight-light">Transacciones de la semana:
            <v-chip small color="blue" class="black--text font-weight-bold">{{$store.getters.transactionsWeek.length}}</v-chip>
          </h5>
          <h5 class="font-weight-light">Transacciones del mes:
            <v-chip small color="red" class="black--text font-weight-bold">{{$store.getters.transactionsMonth.length}}</v-chip>
          </h5>
        </div>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data () {
    return {
      loading: false
    }
  },
  computed: {
    user () {
      return this.$store.state.user
    },
    rate () {
      return this.$store.state.parameters.rate
    }
  },
  methods: {
    update () {
      this.loading = true
      setTimeout(() => {this.loading = false}, 3000)
    }
  }
}
</script>
